import NavigationBar from "@/common/currency/NavigationBar.vue";
import SubmitBtn from "@/common/currency/SubmitBtn";
import {
  getAreaList,
  getWorkLabelList,
  workOrderAdd,
  getWorkOrderDetail,
  updateWorkOrder
} from "@/services/servers/compaintReporting";
import { Toast,Dialog } from "vant";
import { store } from "@/utils/storage";
import { mapState } from "vuex";

export const complaintReportingAdd = {
  data() {
    return {
      btnFlag: 0,
      status: 0,
      backImg: require("@/assets/images/common/nav_return_white.png"),
      title: "报事报修",
      isUpdate: 0,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      userInfo: {
        OWNER_CODE: "2019032112344154738",
        CITY_CODE: "2018012400001",
        CITY_NAME: "青岛市"
      },
      imgShow: false,
      typeFlag: 0,
      complateDetail: {},
      labelHeader: "请选择问题标签",
      questionHeader: "问题描述",
      imgType: "1",
      content: "",
      flag: 0,
      areaActive: false,
      pullDownIcon: require("@/assets/images/home/property_order/whitedown.png"),
      chooseAreaTitle: "请选择房间",
      chooseArea: {},
      headerSTyle: {
        background: "rgba(0,0,0,0)",
        color: "#F9F9F9",
        boxShadow: "none"
      },
      areaLabelList: [],
      areaList: [],
      areaIndex: 0,
      num: 0,
      labelList: [],
      activeLabel: -1,
      deleteImg: require("@/assets/images/common/icon_delete_image.png"),
      uploadImg: require("@/assets/images/common/upload_img.png"),
      uploadImgList: [],
      imgIndex: 0,
      uploadImgLists: [],
      uploadImgSuc: [],

      OWNER_CODE: "2019032112344154738",
      CITY_CODE: "2018012400001",
      CITY_NAME: "青岛市",
      topStyle: {
        "padding-top": this.$stbHeight + "px",
        "box-shadow": "none",
        "min-height": "44px"
      },
      isShowVoiceBtn: false, //语音搜索入口
      isPressSpeechBtn: false, //按下长按说话按钮
      isShowSpeechDiv: false, //语音搜索按下时显示语音提界面
      isNoRecognizeResult: false, //没有识别结果
      isShowSpeechResult: false, //显示语音识别结果
      isHasAudioPermissions: false, //判断是否有语音权限（Android用）
      recognizeResult: "正在聆听"
    };
  },
  components: {
    NavigationBar,
    SubmitBtn
  },
  watch: {
    areaActive(val) {
      this.$store.dispatch("setPopWinFlag", val);
      console.log(this.closePopWin);
    },
    imgShow(val) {
      this.$store.dispatch("setPopWinFlag", val);
      console.log(this.closePopWin);
    },
    closePopWin: {
      handler(newName, oldName) {
        this.imgShow = false;
        this.areaActive = false;
        this.$store.dispatch("setClosePopWinFlag", false);
      },
      immediate: true
    }
  },
  computed: {
    ...mapState({
      isShowHomeWindow: state => state.productPage.isShowHomeWindow,
      closePopWin: state => state.home.closePopWin
    }),
    // topStyle:function(){
    //     let pv = this.$stbHeight + 270 + 'px'; //24px
    //     return{'height':pv}
    // },
    boxStyle: function() {
      let pv = this.$stbHeight; //24px
      // if(pv > 10){
      //     pv = 10;
      // }
      pv = pv + "px";
      return { "padding-top": pv };
    },
    boxStyle2: function() {
      let pv = 0;
      if (this.$devicePlatform == "android") {
        pv = this.$stbHeight;
      } //24px
      // if(pv > 10){
      //     pv = 10;
      // }
      pv = pv + "px";
      return { "padding-top": pv };
    },
    boxStyle3: function() {
      let pv = 0;
      if (this.$devicePlatform == "android") {
        // pv = this.$stbHeight;
        // pv = pv + 'px';
        return { "padding-top": 0 };
      }
    },
    boxStyle1: function() {
      let pv = this.$stbHeight; //24px
      // if(pv > 10){
      //     pv = 10;
      // }
      pv = pv + "px";
      return { "padding-top": pv };
    },
    //超过最大数不显示添加
    accountAddImage() {
      return this.uploadImgList < 4;
    },

    //相册选择图片最大数值
    maximumImagesCount() {
      return 4 - this.uploadImgList.length;
    }
  },
  mounted() {
    this.typeFlag = this.$route.query.typeFlag;
    if (!this.$route.query.complateCode) {
      this.activeLabel = 0;
    }
    if (this.typeFlag == 0) {
      this.title = "报事报修";
    } else {
      this.title = "投诉建议";
    }

    this.initData();
    cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);
    // this.checkAudioPermissions();
    // window.addEventListener("native.keyboardshow", this.keyboardshow);
    // window.addEventListener("native.keyboardhide", this.keyboardhide);
  },
  beforeRouteLeave(to, from, next) {
    this.keyboardhide();
    // window.removeEventListener("native.keyboardshow", this.keyboardshow);
    // window.removeEventListener("native.keyboardhide", this.keyboardhide);
    next();
  },
  methods: {
    // 初始化数据
    initData() {
      let appCode = "";
      if (this.typeFlag == 0) {
        appCode = "0008";
      } else {
        appCode = "0009";
      }
      let areaData = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        CITY_CODE: this.$userInfo.CITY_CODE,
        CITY_NAME: this.$userInfo.CITY_NAME,
        APP_CODE: appCode
      };
      let that = this;
      let labelList = {};
      labelList.type = this.typeFlag;
      getWorkLabelList(labelList).then(
        res => {
          if (res.resultCode == "0") {
            that.labelList = res.data;
          } else {
          }
        },
        error => {}
      );
      // areaData.OWNER_CODE = this.OWNER_CODE;
      // areaData.CITY_CODE = this.CITY_CODE;
      // areaData.CITY_NAME = this.CITY_NAME;

      getAreaList(areaData).then(
        res => {
          this.areaList = res;
          that.areaLabelList = [];
          this.areaList.forEach((value, index) => {
            this.areaLabelList.push(value.showAllName);
            if (!this.$route.query.complateCode) {
              if (value.ROOM_CODE == this.$userInfo.ROOM_CODE) {
                this.chooseAreaTitle = value.showAllName;

                this.chooseArea = value;
                this.areaIndex = index;
              }
            } else {
              if (this.$route.query.complateCode) {
                this.init(this.$route.query.complateCode);
                this.isUpdate = 1;
              }
            }
          });
        },
        error => {}
      );
    },
    // 获取工单详情
    init(complateCode) {
      let params = {
        complCode: complateCode
      };
      if (this.flag == 0) {
        this.flag = 1;
        getWorkOrderDetail(params).then(
          res => {
            this.flag = 0;
            let con = res.data;
            this.complateDetail = { ...con };
            this.content = con.content;

            this.labelList.forEach((value, index) => {
              if (value.labelName == con.labelName) {
                this.activeLabel = index;
              }
            });
            this.uploadImgList = con.picUrlList;
            let that = this;
            con.picUrlList.forEach((item, index) => {
              let arr = {};
              arr.index = index;
              arr.img = item;
              that.uploadImgLists.push(arr);
            });
            console.log(this.uploadImgLists);
            this.areaList.forEach((value, index) => {
              if (value.ROOM_CODE == con.roomCode) {
                this.chooseAreaTitle = value.showAllName;
                this.chooseArea = value;
                this.areaIndex = index;
              } else {
              }
            });
          },
          err => {}
        );
      }
    },
    // 返回
    onLeftClick() {
      cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
      this.$router.go(-1);
    },
    openImg() {
      console.log(this.imgShow);
      this.imgShow = true;
    },

    // 激活小区选中
    activationArea() {
      this.areaActive = true;
    },
    addSelect(item, index) {
      console.log(item);
      console.log(this.maximumImagesCount);
      if (item.name == "拍照") {
        this.useCamera();
      } else if (item.name == "从手机相册选择") {
        this.getPicture();
      }
    },

    // 底部选中小区
    onConfirm(value, index) {
      this.chooseAreaTitle = value;
      this.areaIndex = index;
      this.chooseArea = this.areaList[index];
      console.log(this.chooseArea);
      this.areaActive = false;
    },
    // 底部取消操作
    onCancel() {
      this.areaActive = false;
    },

    // 选中标签
    chooseLabel(index) {
      this.activeLabel = index;
    },
    //提交
    submit() {
      let that = this;
      let info = this.labelList[this.activeLabel];
      if (this.content.length <= 0) {
        Toast.setDefaultOptions({ duration: 1000 });
        Toast("请填写详细问题");
        return;
      }

      let imgArrs = [];
      this.uploadImgLists.forEach(item => {
        imgArrs.push(item.img);
      });
      if (this.uploadImgLists.length != this.uploadImgList.length) {
        Toast("图片正在上传请稍候");
        return;
      } else {
      }

      let data = {
        terminalType: "3",
        typeFlag: this.typeFlag,
        labelCode: info.labelCode,
        reportType: "0",
        content: this.content,
        picUrl: imgArrs,
        areaCode: this.chooseArea["AREA_CODE"],
        buildCode: this.chooseArea["BUILD_CODE"],
        roomCode: this.chooseArea["ROOM_CODE"],
        areaName: this.chooseArea["AREA_NAME"],
        buildName: this.chooseArea["BUILD_NAME"],
        unitNo: this.chooseArea["UNIT_NO"],
        roomName: this.chooseArea["ROOM_NAME"],
        ownerCode: this.$userInfo.OWNER_CODE,
        ownerName: this.$userInfo.OWNER_NICK_NAME,
        ownerPhone: this.$userInfo.PHONE,
        createdBy: this.$userInfo.OWNER_CODE
      };
      // data = JSON.stringify(data);
      workOrderAdd(data).then(
        res => {
          console.log(res);
          if (res.resultCode == "0") {
            cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
            that.$router.go(-1);
          } else {
            Toast(res.msg);
          }
        },
        error => {}
      );
    },
    // 修改
    update() {
      let that = this;
      let info = this.labelList[this.activeLabel];
      let imgArrs = [];
      this.uploadImgLists.forEach(item => {
        imgArrs.push(item.img);
      });
      if (this.uploadImgLists.length != this.uploadImgList.length) {
        Toast("图片正在上传请稍候");
        return;
        this.num++;
      } else {
      }
      let data = {
        complCode: this.complateDetail.complCode,
        ownerCode: this.$userInfo.OWNER_CODE,
        labelCode: info.labelCode,
        content: this.content,
        picUrl: imgArrs,
        updatedBy: this.$userInfo.OWNER_CODE
      };
      updateWorkOrder(data).then(
        res => {
          if (res.resultCode == "0") {
            Toast("修改成功");
            that.$router.go(-1);
          } else {
            Toast(res.msg);
          }
        },
        err => {}
      );
    },
    // 删除图片列表
    deleteUploadImg(index) {
      this.uploadImgList.splice(index, 1);
      this.uploadImgLists.splice(index, 1);
    },

    /**
     * 调用摄像头插件进行拍照
     * @param
     **/
    useCamera() {
      // let options ={
      //     quality : 90,
      //     destinationType : Camera.DestinationType.DATA_URL,
      //     sourceType : Camera.PictureSourceType.CAMERA,     //照相机类型
      //     allowEdit : true,
      //     encodingType : Camera.EncodingType.JPEG,
      //     targetWdith : 100,
      //     targetHeight : 100,
      //     popoverOptions : CameraPopoverOptions,
      //     saveToPhotoAlbum : false
      // };
      let that = this;
      this.$plugin.usePlugin("camera")(
        true,
        imageData => {
          if (this.$devicePlatform == "android") {
            //裁剪图片
            this.$plugin.usePlugin("crop")(
              imageData,
              url => {
                //success callback
                console.log("yuanl=> 裁剪url = " + url);
                url = url.split("?");
                that.uploadImgList.push(url[0]);
                that.uploadFile(url[0], that.imgIndex, 0);
                that.imgIndex++;
              },
              error => {
                //fail callback
                console.log("Crop error. " + JSON.stringify(error));
                if (error.recapture == "true") {
                  console.log("调用相机重拍. ");
                  this.useCamera(needCropImg);
                } else if (error.message == "User cancelled") {
                  console.log("用户取消裁剪操作. ");
                } else {
                  this.$toast("裁剪图片出现异常，请重试！");
                }
              }
            );
          } else {
            that.uploadImgList.push(imageData);
            that.uploadFile(imageData, that.imgIndex, 0);
            that.imgIndex++;
          }
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-6708
        }
        // options
      );
    },

    uploadFile(fileURL, index) {
      let that = this;
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var success = function(result) {
        let resData = JSON.parse(result.response);

        console.log("qiao " + result + " JSON: " + JSON.stringify(resData));
        if (resData.resultCode == 0) {
          let con = {};
          con.index = index;
          con.img = resData.data;
          that.uploadImgLists.push(con);
        } else {
          that.uploadFile1(fileURL, index);
        }
      };
      var fail = function(error) {
        that.uploadFile1(fileURL, index);
      };
      var options = new FileUploadOptions();
      var params = {};
      params.file = fileURL;
      params.source = "yzApp";
      params.business = "complPic";
      options.params = params;
      var headers = { contentType: "multipart/form-data" };
      options.headers = headers;

      // this.$plugin.usePlugin("fileTransfer")(fileURL,encodeURI(reqUrl),success,fail,options);
      var ft = new FileTransfer();
      ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
    },

    uploadFile1(fileURL, index) {
      let num = index;
      let that = this;
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var success = function(result) {
        let resData = JSON.parse(result.response);
        console.log("qiao " + result + " JSON: " + JSON.stringify(result));
        if (resData.resultCode == 0) {
          let con = {};
          con.index = index;
          con.img = resData.data;
          that.uploadImgLists.push(con);
        } else {
          Toast("图片上传失败");
          console.log(num);
          console.log(that.uploadImgList);
          that.uploadImgList.splice(num, 1);
          that.imgIndex = that.uploadImgList.length - 1;
          if (that.imgIndex < 0) {
            that.imgIndex = 0;
          }
          if (that.uploadImgLists.length == 0) {
            that.uploadImgList = [];
          }
        }
      };
      var fail = function(error) {
        Toast("图片上传失败");
        console.log(num);
        console.log(that.uploadImgList);
        that.uploadImgList.splice(num, 1);
        that.imgIndex = that.uploadImgList.length - 1;
        if (that.imgIndex < 0) {
          that.imgIndex = 0;
        }
        if (that.uploadImgLists.length == 0) {
          that.uploadImgList = [];
        }
      };
      var options = new FileUploadOptions();
      var params = {};
      params.file = fileURL;
      params.source = "yzApp";
      params.business = "complPic";
      options.params = params;
      var headers = { contentType: "multipart/form-data" };
      options.headers = headers;

      // this.$plugin.usePlugin("fileTransfer")(fileURL,encodeURI(reqUrl),success,fail,options);
      var ft = new FileTransfer();
      ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
    },

    /**
     * 从本地相册进行上传
     **/
    getPicture() {
      let that = this;
      let options = {
        quality: 90,
        destinationType: Camera.DestinationType.DATA_URL,
        sourceType: Camera.PictureSourceType.CAMERA, //照相机类型
        allowEdit: true,
        encodingType: Camera.EncodingType.JPEG,
        targetWdith: 100,
        targetHeight: 100,
        popoverOptions: CameraPopoverOptions,
        saveToPhotoAlbum: false
      };

      this.$plugin.usePlugin("getPictures")(
        that.maximumImagesCount,
        images => {
          images.forEach(imageData => {
            that.uploadImgList.push(imageData);
            that.uploadFile(imageData, that.imgIndex, 0);
            that.imgIndex++;
          });
        },
        error => {
          //Toast(error); //delete for ZHSQYYPT-6708
        },
        options
      );
    },

    //
    lineCancel() {
      this.imgShow = false;
    },
    //语音搜索
    goToSpeech(e) {
      var _this = this;
      this.pressTimer = null;
      _this.isNoRecognizeResult = false;
      this.isShowSpeechResult = false;
      var ifly = cordova.plugins.IflyForXwjPlugin;
      _this.recognizeResult = "正在聆听";
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      if (e.type === "click" && e.button !== 0) {
        return;
      }
      this.isPressSpeechBtn = true;
      this.$refs.voiceBtnKeyBoard.style.background = "#D34050";
      this.isNoRecognizeResult = false;
      if (_this.isHasAudioPermissions) {
        if (this.pressTimer === null) {
          this.pressTimer = setTimeout(() => {
            _this.sendVal = true;
            this.isShowSpeechDiv = true;
            ifly.startListen(null, null, false, true);
          }, 300);
        }
      } else {
        Dialog.confirm({
          message: "请您打开麦克风权限，以正常使用语音识别功能"
        })
          .then(() => {
            _this.checkAudioPermissions();
          })
          .catch(() => {
            //oncancel
          });
      }
    },
    stopSpeech() {
      console.log("这里执行了吗--stopSpeech");
      var _this = this;
      var ifly = cordova.plugins.IflyForXwjPlugin;
      this.isPressSpeechBtn = false;
      this.$refs.voiceBtnKeyBoard.style.background = "#FFFFFF";
      console.log("--------3");
      if (_this.sendVal) {
        _this.sendVal = false;
        console.log("--------1");
        ifly.stopListen(
          function(spRes) {
            console.log("success " + spRes);
            if (spRes) {
              var resultcatch = spRes.replace(
                /[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,
                ""
              );
              resultcatch = spRes;
              _this.recognizeResult = resultcatch;
              _this.content = _this.content + resultcatch;
              _this.isShowSpeechResult = true;
              //收起键盘
              setTimeout(() => {
                _this.isShowSpeechDiv = false;
                _this.$refs.textArea.focus();
                _this.changeSpeechBtnVisible();
              }, 1000);
            } else {
              _this.isNoRecognizeResult = true;
              setTimeout(() => {
                _this.isShowSpeechDiv = false;
                _this.$refs.textArea.focus();
                _this.changeSpeechBtnVisible();
              }, 1000);
            }
          },
          function(errMes) {
            console.log("--------4");
            _this.isNoRecognizeResult = true;
            setTimeout(() => {
              _this.isShowSpeechDiv = false;
              _this.$refs.textArea.focus();
              _this.changeSpeechBtnVisible();
            }, 1000);
            console.log("err" + errMes);
          }
        );
      } else {
        _this.sendVal = false;
        _this.isShowSpeechDiv = false;
        setTimeout(() => {
          _this.changeSpeechBtnVisible();
          _this.$refs.textArea.focus();
        }, 1000);
      }
      console.log("停止录音 " + this.pressTimer);
      clearTimeout(this.pressTimer);
      this.pressTimer = null;
    },
    checkAudioPermissions() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.isHasAudioPermissions = true;
        return true;
      } else {
        var list = ["android.permission.RECORD_AUDIO"];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.isHasAudioPermissions = true;
            }
          },
          function(status) {
            alert("请打开麦克风权限");
            _this.isHasAudioPermissions = false;
          }
        );
      }
    },
    changeSpeechBtnVisible() {
      if (this.$devicePlatform == "android") {
        this.isShowVoiceBtn = false;
      }
    },
    /**
     * 键盘弹出事件
     */
    keyboardshow(e) {
      this.isShowVoiceBtn = true;
      this.$refs.voiceBtnKeyBoard.style.bottom = e.keyboardHeight + 12 + "px";
    },

    /**
     * 键盘隐藏事件
     */
    keyboardhide() {
      if (!this.sendVal) {
        this.isShowVoiceBtn = false;
      }
    }
  }
};
