<template>
    <div class="submit">
        <div id="submit" :class="{submitBtn:true,submitDown:isDown,forbidBtn:isForbid,submitBtnThemeL:bindThemeL}" @click="submit" @touchstart="touchStart" @touchend="touchEnd">{{submitName}}</div>
    </div>
</template>

<script>
export default {
    props:{
        // 按钮名称
        submitName:{
            type:String,
            default:'提交',
        },
        // 是否禁用按钮
        isForbid:{
            type:Boolean,
            default:false,
        }
    },
    data(){
        return{
            isDown:false,
            bindThemeL: this.$curTheme==0 ? false : true
        }
    },
    methods:{
        submit(){
            this.$emit('save', '33')
        },
        touchStart(){
            if(this.isForbid == false){
                this.isDown = true;
            }
            
        },
        touchEnd(){
            if(this.isForbid == false){
                this.isDown = false;
            }
        }
    }
}
</script>

<style scoped>
.submit{
    width: 100%;
    height: 70px;
    line-height: 70px;
    overflow: hidden;
}
.submit .submitBtn{
    width: 86%;
    height: 44px;
    line-height: 44px;
    text-align: center;
    margin: 14px auto;
    color: #fff;
    font-size: 16px;
    background: #FB5768;
    box-shadow: 0 0 15px 0 rgba(197,58,72,0.39);
    border-radius: 22px;
}
.submit .submitBtnThemeL{
    font-size: 18px;
}
.submit .submitDown{
    background: #D42C45;
}
.submit .forbidBtn{
    background: #FDACB5;
}
</style>